<template>
	<view class="poker-main" :class='[mainPanelClass]'>
		<div v-for='(card, idx) of cards' :key='card.index' class="card" :style='[cardStyle(card, idx)]' @click='cardClicked(card)'></div>
		
		<div class="control-bar">
			<div class="button" @click='pickCard'>捡</div>
			<div class="button">要牌</div>
		</div>
		
		<div class="welcome" @click='show.welcome = false; newGame()'></div>
	</view>
</template>

<script>
	const fapaiWait = 100
	const createCards = (part=Math.random()) => {
		const arr = []
		
		arr.push({ // 小王
			type: 4,
			num: 0,
			part
		})
		arr.push({ // 大王
			type: 4,
			num: 1,
			part
		})
		
		for (let type=0; type<4; type++) {
			for (let num=0; num<13; num++) {
				arr.push({
					type,
					num,
					part
				})
			}
		}
		
		return arr;
	}
	
	const mess = arr => {
		let i = arr.length - 1
		while (i>0) {
			const j = Math.floor(Math.random() * i)
			let tmp = arr[i]
			arr[i] = arr[j]
			arr[j] = tmp
			i--
		}
		for (let idx=0; idx<arr.length; idx++) {
			arr[idx].index = idx
		}
		return arr
	}
	
	
	const indexOf = (card, arr) => {
		for (let idx=0; idx<arr.length; idx++) {
			const cardInArr = arr[idx]
			if (cardInArr) {
				if (cardInArr.type === card.type && cardInArr.num === card.num && cardInArr.part === card.part) {
					return idx
				}
			}
			
		}
		return -1
	}
	
	const wait = (num) => {
		return new Promise(res => {
			setTimeout(res, num)
		})
	}
	
	export default {
		data() {
			return {
				show: {
					welcome: true
				},
				cards: [],   // 剩余可用的牌
				red: [],      // 我方手里的牌
				blue: [],     // 电脑手里的牌\
				bluePicked: [], 
				ground: [],    // 场上可以捡的牌
				picked: false, // 在场上捡的那个牌
				
				red2: [], // 记录我方当前选择的牌
				red2Idx: 0, // 如果添加需要添加到哪里
				redPicked: [],
				
				redTurn: false,  // 我方可以操作
			}
		},
		onLoad() {
			
		},
		
		computed: {
			mainPanelClass() {
				const {
					welcome
				} = this.show
				
				const arr = []
				if (welcome) {
					arr.push('show-welcome')
				}
				return arr
			},
			pickedSum() {
				const sum = this.red2.reduce((acc, cc) => {
					return acc + cc.num + 1
				}, 0)
				return sum
			}
		},
		
		watch: {
			pickedSum(value) {
				if (this.picked) {
					if (value + this.getCardValue(this.picked) !== 14) {
						this.picked = false
					}
				}
			}
		},
		
		methods: {
			pickCard() {
				const {
					picked,
					red2
				} = this
				if (picked) {
					this.redPicked.push(picked)
					for (const card of red2) {
						if(card) {
							this.redPicked.push(card)
						}
					}
					const newRed = []
					for(const cardInRed of this.red) {
						const found = this.red2.find(cc => {
							return cc && cc.index === cardInRed.index
						})
						if (!found) {
							newRed.push(cardInRed)
						}
					}
					this.red = newRed;
					
					const newGround = []
					for (const cardInGround of this.ground) {
						if(cardInGround.index !== picked.index) {
							newGround.push(cardInGround)
						}
					}
					this.ground = newGround
					
					this.picked = false
					this.red2 = []
					
				}
			},
			
			getCardValue(card) {
				let num = card.num + 1
				if (card.type === 4) {
					num = 5
				}
				return num
			},
			
			cardClicked(card) {
				const that = this
				if (!that.redTurn) { // 用户当前不能操作
					return false
				}
				const groundIdx = indexOf(card, this.ground)
				if (!(indexOf(card, that.red) >=0 || groundIdx >= 0)) { // 只能点我方的牌, 和场上的牌
					return false
				}
				
				if (groundIdx >= 0) {
					if (that.getCardValue(card) + this.pickedSum  === 14) {
						that.picked = card
					}
				} else {
					const redIdx = indexOf(card, that.red2)
					if(redIdx >= 0) { // 已经存在
						const arr = []
						for (let idx=0; idx<that.red2.length; idx++) {
							
							if (idx != redIdx && that.red2[idx]) {
								arr.push(that.red2[idx])
							}
						}
						that.red2 = arr
						that.red2Idx = 1
					} else {
						const arr = JSON.parse(JSON.stringify(that.red2))
						arr[that.red2Idx] = card
						that.red2 = arr
						that.red2Idx++;
						that.red2Idx = that.red2Idx % 2
					}
				}
				
				
				
				
			},
			newGame() {
				const that = this
				that.cards = mess([...createCards(1), ...createCards(2)])
				that.red = []
				that.blue = []
				that.ground = []
				that.cardIndex = 0
				
				
				setTimeout(() => {
					const fapaile = async () => {
						
						for (let idx=0; idx<8; idx+=2) {
							await wait(fapaiWait)
							that.red.push(that.cards[idx])
							await wait(fapaiWait)
							that.blue.push(that.cards[idx + 1])
						}
						
						for (let idx=8; idx<12; idx++) {
							await wait(fapaiWait)
							that.ground.push(that.cards[idx])
						}
						that.redTurn = true
					}
					fapaile();
					
					
				}, 100)
			},
			cardStyle(card, idx) {
				const that = this
				const {
					red,
					blue,
					ground
				} = that
				
				let top = 400
				let left = 600
				
				let leftOffset = 10
				let cardWidth = 140
				
				let found = false
				const redIdx = indexOf(card, red)
				if (redIdx>=0) {
					top = 1000
					left = leftOffset + redIdx * cardWidth
					found = true
					if (indexOf(card, that.red2) >= 0) {
						top = 950
					}
				}
				
				const blueIdx = indexOf(card, blue)
				if (blueIdx>=0) {
					top = 10
					left = leftOffset + blueIdx * cardWidth
					found = true
				}
				
				const groundIdx = indexOf(card, ground)
				let cardInGround = false
				if (groundIdx >= 0) {
					found = true
					const columnCount = 4
					const rowIdx = Math.floor(groundIdx / columnCount)
					const colIdx = groundIdx %  columnCount
					
					top = 400 + rowIdx * cardWidth
					left = leftOffset + colIdx * cardWidth
					if(that.picked) {
						if (that.picked.index === card.index) {
							top -= 50
						}
					}
					cardInGround = true
				}
				
				
				const style = {
					'background-position': found ? `${1770 - card.num * 136}rpx ${1028 - card.type * 205}rpx` : '1960rpx 400rpx',
					top: `${top}rpx`,
					left: `${left}rpx`,
					'z-index': 10001 + idx
				}
				
				if (cardInGround) {
					const {
						red2
					} = that
					if (red2.length > 0) {
						if (that.getCardValue(card) + that.pickedSum  === 14) {
							
						} else {
							style.filter = 'brightness(.5)'
						}
					}
					
					if(that.picked) {
						if (that.picked.index === card.index) {
							style.transform = 'scale(1.1)'
						}
					}
				}
				
				return style
			}
		}
	}
</script>

<style>
	.poker-main {
		height: 100vh;
		width: 100vw;
		background: hsl(120, 100%, 30%);
		z-index: 10000;
	}

	.card {
		background-image: url(data:image/jpeg;base64,);
		width: 136rpx;
		height: 205rpx;
		background-size: 2000rpx 1028rpx;
		display: inline-block;
		margin: 1rpx;
		border-radius: 4rpx;
		position: absolute;
		transition: top 1s, left 1s;
		font-size: 80rpx;
		color: red;
	}
	
	.welcome {
		height: 0;
		width: 100vh;
		background: white;
		position: absolute;
	}
	
	.show-welcome > .welcome{
		top: 0;
		height: 100vh;
	}
	
	.control-bar {
		position: absolute;
		width: 90%;
		bottom: 20rpx;
		display: flex;
		justify-content: center;
	}
	
	.control-bar > .button {
		padding: 20rpx;
		border: 1rpx solid gray;
		background: lightgray;
		border-radius: 20rpx;
		margin: 10rpx;
	}
</style>
